.header {
  display: flex;
  justify-content: space-between;
  padding: 10px 100px;
  box-shadow: 0px 2px 6px var(--box-shadow);
  background-color: var(--background);
  position: sticky;
  top: 0;
  z-index: 99;

  h1 {
    font-size: 1.7rem;
    font-weight: 700;

    * {
      text-decoration: none;
      color: var(--text-primary);
    }
  }
}

@media(max-width:40em) {
  .header{
    padding: 10px 25px;
    h1{
      font-size: 1.5rem;
      
    }
  }
}
