.filterContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 43px 100px;
    background-color: var(--background);
    z-index: 1
}

@media(max-width:40em) {
    .filterContainer{
        flex-direction: column;
        padding: 43px 20px;
    }
}

